$color-primary: #fcd51b;
$color-text: #000;
$color-shadow: rgba(0,0,0,0.16);
$color-background: #f3f3f3;
$color-calender-background: #f4f2e8;

/* Set defaults */
* {
  margin: 0;
  padding: 0;
}
body {
  background-color: $color-background;
}

/* Set Primary Colors */
nav, footer, .copy {
  background-color: $color-primary;
}
nav a {
  text-decoration: none;
  color: $color-text;
}

/* Primary navigation */
.nav-top {
  height: 60px;
  position: fixed;
  top: 0;
  width: 100vw;
  box-shadow: 0 3px 6px $color-shadow;
  overflow: hidden;
  z-index: 10;
}
.nav-top ul {
  list-style-type: none;
}
.nav-top li {
  float: left;
}
.nav-top span {
  text-align: center;
  display: inline-block;
  width: 150px;
  height: 60px;
  margin-top: -6%;
  font-size: 1.2em;
  vertical-align: middle;
}
.nav-top img {
  width: 60px;
  height: 60px;
}

/* Navigation Toggle */
#nav-open {
  float: right;
  height: 60px;
  margin-right: 30px;
  font-size: 1.2em;
  border: none;
  background-color: transparent;
}
#nav-close {
  height: 60px;
  margin-left: 30px;
  font-size: 1.2em;
  border: none;
  background-color: transparent;
}

/* Secondary Navigation */
.nav-side {
  position: fixed;
  top: 0;
  width: 200px;
  height: 100%;
  box-shadow: -3px 0 6px $color-shadow;
  transition: all 0.15s ease-in-out;
  z-index: 100;
}
.nav-side.visible {
  right: 0;
}
.nav-side.hidden {
  right: -203px;
}
.nav-side ul{
  list-style-type: none;
}
.nav-side li {
  border-top: 1px solid rgba(255,255,255,0.4);
  padding-left: 30px;
  line-height: 50px;
}
.nav-side .lang {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

/* Footer */
footer {
  box-shadow: 3px 0 6px $color-shadow;
}
footer p {
  line-height: 40px;
  text-align: center;
  border-bottom: 2px solid black;
}
div.footer-info-left, div.footer-info-right {
  width: 50%;
  float: left;
  line-height: 30px;
}
div.footer-info ul {
  margin: 20px 0 20px;
  list-style-type: none;
}
div.footer-info-left ul {
  float: right;
  width: 50%;
  padding-bottom: 30px;
  border-bottom: 1px solid black;
}
div.footer-info-right ul {
  width: 50%;
  text-align: right;
  padding-bottom: 30px;
  border-bottom: 1px solid black;
}
div.social ul {
  list-style-type: none;
  display: block;
  text-align: center;
}
div.social ul li {
  display: inline;
  margin: 0 20px;
}
span.copy {
  text-align: center;
  display: block;
  margin-top: 20px;
  line-height: 50px;
}

/* Main Application */
#app {
  margin: 60px auto 0 auto;
  padding-top: 30px;
  width: 80%;
  min-height: 800px;
  background-color: $color-background;
}
section {
  margin-bottom: 30px;
  text-align: left;
}
img.about-photo {
  width: 300px;
  height: 300px;
  float: left;
  margin-right: 30px;
}
.about-short {
  height: 258px;
  position: relative;
  width: auto;
  text-align: justify;
  border: 1px solid black;
  margin-left: 330px;
  padding: 20px;
  overflow: hidden;
}
.about-short button {
  position: absolute;
  bottom: 0;
  right: 0;
  display: block;
  width: 100%;
  height: 40px;
  border: none;
  background-color: $color-primary;
}

/* Calender */
.clndr {
  width: 50%;
  border: 1px solid black;
}
.clndr-table {
  width: 100%;
  text-align: center;
  font-size: 1.15em;
  height: 300px;
  padding: 20px;
}
.clndr-controls {
  text-align: center;
  position: relative;
  background-color: $color-primary;
  line-height: 50px;
  font-size: 1.2em;
}
.clndr-controls .clndr-control-button {
  position: absolute;
  left: 30px;
}
.clndr-controls .clndr-control-button:last-child {
  position: absolute;
  left: auto;
  right: 30px;
}
.clndr-controls div {
  display: inline-block;
}
.day {
  width: 14%;
  line-height: 16%;
  padding-top: 12px;
}
.adjacent-month {
  opacity: 0.4;
}
.day-contents {
  padding: 10px 8px 6px;
  line-height: 100%;
  height: 1.15em;
  width: 1.15em;
  border-radius: 50%;
  margin: 0 auto;
}
.header-days td{
  padding-bottom: 10px;
}
.today .day-contents {
  border: 1px solid #292363;
}
.event .day-contents {
  border: 1px solid #961a1c;
}
.sekected .day-contents {
  background-color: #f3f3f3;
}
.event.selected .day-contents {
  background-color: #961a1c;
}

/* Reviews */
.review {
  padding: 20px;
  border: 1px solid black;
  background-color: #fff;
  margin-top: 30px;
}
.review:first-child {
  margin: 0;
}
.rev-title {
  font-weight: bold;
  font-size: 1.15em;
  margin-right: 20px;

}
.rev-name {
  font-size: 1.15em;
}
.rev-stars {
  float: right;
}
.rev-content {
  margin-top: 20px;
  text-align: justify;
}








